import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import { get } from "../tool/http.js";
import Obtained from "../component/icon/obtained.jsx";

const qs = require('qs');

const columnData = [
    { id: 'user_name', numeric: true, disablePadding: false, label: '' },
    { id: 'ip', numeric: true, disablePadding: false, label: 'IP' },
    { id: 'price', numeric: true, disablePadding: false, label: '' },
    { id: 'status', numeric: true, disablePadding: false, label: '' },
    { id: 'end_at', numeric: true, disablePadding: false, label: '' },
    { id: 'target_ip', numeric: true, disablePadding: false, label: 'IP' },
    { id: 'operat', numeric: true, disablePadding: false, label: '', extend: true, extendConfirm: {
        rule: {
            term: "status",
            execute: "",
            type: "equal"
          },
        title: "",
        content: "",
        icon: <Obtained />,
        ok: (data) => {
            return new Promise((resolve,reject) => {
                get("defenseip/remove/subExamine",{
                    business_id: data.id,
                }).then((res) => {
                    if(res.data.code==1) {
                        alert(res.data.msg);
                        resolve(res.data);
                    } else {
                        alert(res.data.msg);
                        resolve(res.data);
                    }
                }).catch(reject);
            });
        }
    } }
];

const inputType = [

]

@inject("defenseBusinessStores")
@observer
class DefenseBusinesList extends React.Component {
    componentDidMount() {
        this.props.defenseBusinessStores.getData(qs.parse(location.search.substr(1)).id);
    }

    updata() {
        this.props.defenseBusinessStores.getData(qs.parse(location.search.substr(1)).id);
    }

    render() {
        return (
          <ListTableComponent
            title=""
            operattext="IP"
            inputType={inputType}
            headTitlesData={columnData}
            data={this.props.defenseBusinessStores.defenseBusiness}
            updata={this.updata.bind(this)}
          />
        );
      }
}

export default DefenseBusinesList;
